<template>
  <v-container fluid>
    <v-card elevation="2" :disabled="loadingRecords" :loading="loadingRecords">
      <div class="d-flex">
        <div>
          <v-card-title>Create Ticket</v-card-title>
          <v-card-subtitle
            ><small
              >Note: All required fields is marked with (*)</small
            ></v-card-subtitle
          >
        </div>
        <v-spacer></v-spacer>
        <router-link :to="{ name: 'MyTicket' }" class="mt-5 mr-5"
          >Back</router-link
        >
      </div>
      <v-form ref="form">
        <v-card-text>
          <v-row>
            <v-col cols="12" sm="6" md="4">
              <v-autocomplete
                ref="category"
                v-model="form.category"
                :rules="[rules.required]"
                :items="categories"
                item-text="name"
                item-value="id"
                label="Category*"
                outlined
                dense
              ></v-autocomplete>
            </v-col>

            <v-col cols="12" sm="6" md="4">
              <v-autocomplete
                ref="department"
                v-model="form.department"
                :rules="[rules.required]"
                :items="departments"
                item-text="name"
                item-value="id"
                label="Department*"
                outlined
                dense
              ></v-autocomplete>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="8">
              <v-text-field
                ref="subject"
                v-model="form.subject"
                label="Subject*"
                :rules="[rules.required, rules.minimum(2), rules.maximum(100)]"
                outlined
                dense
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="8">
              <v-textarea
                ref="description"
                v-model="form.description"
                label="Message*"
                :rules="[rules.required, rules.minimum(2), rules.maximum(5000)]"
                outlined
                dense
                rows="4"
              ></v-textarea>
            </v-col>
          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="pa-4">
          <v-btn
            color="success"
            class="text-capitalize"
            @click="confirmationDialog = true"
          >
            Create
          </v-btn>
          <v-btn class="text-capitalize" :to="{ name: 'MyTicket' }">
            Cancel
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
    <confirmation-message
      v-if="confirmationDialog"
      :confirmationData="confirmationData"
      @closeConfirmationDialog="closeConfirmationDialog"
      @onSubmit="onSubmit"
    />
  </v-container>
</template>

<script>
import formRules from "@/helpers/formRules";
import { mapState } from "vuex";
import ConfirmationMessage from "@/components/ConfirmationMessage.vue";

export default {
  components: { ConfirmationMessage },

  data: () => ({
    rules: formRules,
    form: {
      category: null,
      department: null,
      subject: null,
      description: null,
    },
    confirmationDialog: false,
    confirmationData: {
      title: "Are you sure you want to create?",
      text: "This action cannot be undone.",
      closeBtnText: "Cancel",
      closeBtnColor: "gray",
      saveBtnText: "Create",
      saveBtnColor: "success",
    },
  }),

  created() {
    this.$store.dispatch("myTicket/createRecord");
  },

  computed: {
    ...mapState("myTicket", ["categories", "departments", "loadingRecords"]),
  },

  methods: {
    async onSubmit() {
      this.confirmationDialog = false;
      if (this.$refs.form.validate()) {
        await this.$store.dispatch("myTicket/storeRecord", this.form);
        this.$refs.form.reset();
      }
    },

    closeConfirmationDialog() {
      this.confirmationDialog = false;
    },
  },
};
</script>
